@import 'colors';

$border: rgba(0,0,0,0.20);
$text: rgba(0,0,0,0.45);

input{
	outline: none;
	color: $dark;
	font-weight: 500;
	font-size: 16px;
	position: relative;
	height: 100%;
	width: 100%;
}
.form-group{
	margin-bottom: 16px;
}
label, small{
	margin-left: 3px;
}
.input-box{
	margin: 8px 0px 4px 0px;
	border: 1px solid $border;
	display: block;
	height: 38px;
	padding: 8px 12px;
	border-radius: 6px;
	transition: .2s ease-in;
}
.input-box:hover{
	border: 1px solid $secondary;
}
.input-box:focus, .input-box:focus-within{
	border: 1px solid $primary;
	box-shadow: 0 0 0 3px $light_primary;
	transition: .2s ease-in;
}
// label heading

/*------checkbox----*/
.checkbox-group{
	position: relative;
	display: inline-flex;
}
input[type="checkbox"], input[type="radio"]{
	appearance: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	height: 18px;
	width: 18px;
	background-color: $white;
	border: 1px solid $border;
	border-radius: 5px;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: .2s ease-in;
	margin-right: 6px;
}
input[type="checkbox"]:after{
	position: absolute;
	font-family: "Font Awesome 5 Free";
	font-weight: 900;
	content: "\f00c";
	font-size: 10px;
	color: $white;
	display: none;
}
input[type="checkbox"]:hover{
	border-color: $text;
}
input[type="checkbox"]:checked{
	background-color: $primary;
	border: 1px solid transparent;
	box-shadow: 0 0 0 3px $light_primary;
	transition: .2s ease-in;
}
input[type="checkbox"]:checked:after{
	display: block;
	border: 1px solid transparent;

}
label.checkbox-label{
	font-weight: 400;
	cursor: pointer;
}
.checkbox-grp, .radio-grp {
	display: inline-flex;
  	margin: 0px 10px;
  	cursor: pointer;
  	font-size: 16px;
}
.checkbox-container, .radio-container {
	display: flex;
	width: auto;
	margin: 20px 10px;
  	cursor: pointer;
  	font-size: 16px;
}
.checkbox-container[disabled],.radio-container[disabled],.checkbox-grp[disabled],.radio-grp[disabled],[type=checkbox]:disabled,[type=radio]:disabled{
	opacity: 0.7;
	cursor: no-drop;
}


// date
::-webkit-datetime-edit,::-webkit-datetime-edit-fields-wrapper,::-webkit-datetime-edit-text,::-webkit-datetime-edit-month-field,::-webkit-datetime-edit-day-field,::-webkit-datetime-edit-year-field
{
	color: $white;
}
input:focus::-webkit-datetime-edit,input:focus::-webkit-datetime-edit-fields-wrapper,input:focus::-webkit-datetime-edit-text,input:focus::-webkit-datetime-edit-month-field,input:focus::-webkit-datetime-edit-day-field,input:focus::-webkit-datetime-edit-year-field
{
	color: $active_secondary;
}
input:valid::-webkit-datetime-edit,input:valid::-webkit-datetime-edit-fields-wrapper,input:valid::-webkit-datetime-edit-text,input:valid::-webkit-datetime-edit-month-field,input:valid::-webkit-datetime-edit-day-field,input:valid::-webkit-datetime-edit-year-field
{
	color: $active_secondary;
}
// placeholder
input::-webkit-input-placeholder {
	visibility: hidden;
  	color: $muted;
  	font-weight: 500;
}
input:focus::-webkit-input-placeholder {
  	visibility: visible;
  	transition: 1s;
}